import React from 'react';
import './App.css';
import Footer from './Footer';
import Header from './Components/Header';
import Admin from './Components/Admin';
import {HashRouter as Router, Route, Switch, Redirect} from 'react-router-dom';
import Home from './Components/Home';
import SignIn from './Components/SignInComponent';
import Employee from './Components/Employee';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      setHeader: false
    }
  }
  reset = () => {
    this.setState((prevState) => {
      prevState.setHeader = prevState.setHeader === false ? true : false;
      return {
        setHeader: prevState.setHeader
      }
    })
  }
  render() {
    console.log(this)
    return (
      <Router>
        <div className="App">
          <Header set={this.state.setHeader} />
        
          <Switch >
            <Route exact path='/api/v1' render={() => <Home /> }  />
            <Route exact path='/signout' render={() => <Redirect to={{pathname: '/api/v1/auth/signin', state:{header: 'true'}}} /> }  />
            <Route exact path='/api/v1/auth/signin' render={() => <SignIn setHeader={this.reset} /> }  />
            <Route path='/api/v1/admin' render={() => <Admin />} />          
            <Route path='/api/v1/employee' render={ () => <Employee />} />
          </Switch>
          
          <Footer />
        </div>
      </Router>
    );
  }
}

export default App;
