import React from 'react';
import Article from '../Articles/Article';
import Gif from '../Gifs/Gif';
import ls from 'local-storage';

class FeedPage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      start: this.getFeeds(),
      feeds: '',
      isLoaded: false
    }
  }

  getFeeds = () => {
    const url = `https://teamworksng.herokuapp.com/api/v1/feed`;
    fetch(url, {
      mode: 'cors',
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        "Authorization": "Bearer " + ls.get('token')
      }
    })
    .then( res => res.json())
    .then( response => {
      this.setState({feeds: response.data});
    })
  }

  render() {
    let feedPage = [];
    if(this.state.feeds) {
      this.state.feeds.forEach(element => {
        if(element.url) feedPage.push(<Gif gif={element} key={element.id} />);
        if(element.article) feedPage.push(<Article article={element} key={element.id} />);
      });
    }

    return (
      <div> 
        {feedPage}
      </div>
    )
  }

}

export default FeedPage;
